<template>
  <div class="container">
    <el-row class="order_wrap">
      <el-col :span="24" class="order_content">
        <h1>订单号：{{orderId}}</h1>
        <h2><span>下单时间:{{order.create_time}}</span><span class="totalPrice">金额:{{order.total_price}}</span><span>支付方式:{{order.payment_type}}</span><span>订单状态:{{order.status}}</span></h2>
        <div class="groupBox">
          <h2>联系人信息</h2>
          <table class="table" border="1">
            <tr>
              <th>姓名</th>
              <td><el-input
                placeholder="请输入内容"
                v-model="user.user_name"
                clearable>
              </el-input></td>
              <th>电话</th>
              <td><el-input
                placeholder="请输入内容"
                v-model="user.phone"
                clearable>
              </el-input></td>
            </tr>
            <tr>
              <th>国家/地区</th>
              <td><el-input
                placeholder="请输入内容"
                v-model="user.country"
                clearable>
              </el-input></td>
              <th>省/市</th>
              <td><el-input
                placeholder="请输入内容"
                v-model="user.province"
                clearable>
              </el-input></td>
            </tr>
            <tr>
              <th>县</th>
              <td><el-input
                placeholder="请输入内容"
                v-model="user.city"
                clearable>
              </el-input></td>
              <th>区</th>
              <td><el-input
                placeholder="请输入内容"
                v-model="user.area"
                clearable>
              </el-input></td>
            </tr>
            <tr>
              <th>地址</th>
              <td><el-input
                placeholder="请输入内容"
                v-model="user.address"
                clearable>
              </el-input></td>
              <th>邮政编码</th>
              <td><el-input
                placeholder="请输入内容"
                v-model="user.post_code"
                clearable>
              </el-input></td>
            </tr>
            <tr>
              <th>邮箱</th>
              <td><el-input
                placeholder="请输入内容"
                v-model="user.email"
                clearable>
              </el-input></td>
              <th>备注</th>
              <td><el-input
                placeholder="请输入内容"
                v-model="user.comment"
                clearable>
              </el-input></td>
            </tr>
            <tr>
              <th>IP</th>
              <td>{{user.user_ip}}</td>
            </tr>
          </table>
        </div>
        <div class="groupBox">
          <h2>订单信息</h2>
          <el-table
            :data="orderGoods"
            border
            style="width: 100%">
            <el-table-column
              label="图片">
              <template slot-scope="scope">
                <img width="100" :src="scope.row.img" alt="">
              </template>
            </el-table-column>
            <el-table-column
              prop="sku_code"
              label="SKU">
            </el-table-column>
            <el-table-column
              width="250"
              label="属性">
              <template slot-scope="scope">
                <div v-for="(attrItem,index) in scope.row.attr_list" class="attrBox">
                  <label style="display: inline-block;width: 65px;text-align: right">{{attrItem.attr}}：</label>
                  <el-select v-model="scope.row.attr_one[index].value" size="small" class="w150" @change="attrChange(scope)">
                    <el-option
                      v-for="item in attrItem.value"
                      :key="item"
                      :label="item"
                      :value="item">
                    </el-option>
                  </el-select>
                </div>
              </template>
            </el-table-column>
            <el-table-column
              prop="price"
              label="单价">
              <template slot-scope="scope">
                <el-input v-model="scope.row.price" placeholder="请输入单价"></el-input>
              </template>
            </el-table-column>
            <el-table-column
              prop="number"
              label="数量">
              <template slot-scope="scope">
                <el-input v-model="scope.row.number" placeholder="请输入数量"></el-input>
              </template>
            </el-table-column>
            <el-table-column
              label="操作">
              <template slot-scope="scope">
                <el-button type="danger" size="small" @click.native="deleteGoods(scope)">删除</el-button>
              </template>
            </el-table-column>
          </el-table>
        </div>
      </el-col>
      <div class="footerButton border-shadow">
        <el-button type="primary" @click="submit" style="vertical-align: top">提交修改</el-button>
      </div>
    </el-row>
  </div>
</template>

<script>
  const orderStatus = ['没状态','待确认','已确认','待采购','已采购','待发货','捡货中','已打包','已发货','已签收','已取消','已回款','已退款','拒签','丢件','问题件','待转运','缺货','到货待确认','缺货取消','待处理','待通过'];

  export default {
    name: "updateOrder",
    data() {
      return {
        order: {
          create_time: '',//下单时间
          total_price: 0,//金额
          payment_type: '',//支付方式
          status: ''//订单状态
        },
        user: {
          user_name : '',
          phone : '',
          country: '',
          province: '',
          city: '',
          area: '',
          address: '',
          post_code: '',
          comment: '',
          user_ip: ''
        },
        orderGoods: [],
        // 选择后发送后台查询 sku img
        send_attr:[
          {attr:'color', value:'black'},
          {attr:'size', value:'S'},
        ],
        // 所有属性组合
        attr_data:[
          {attr:'color', value:['black', 'white']},
          {attr:'size', value:['L', 'S']},
        ],
      }
    },
    methods: {
      submit() {
        let url = '/v1/order/update-order';
        let Orders = {};
        Orders.id = this.orderId;
        Orders.name = this.user.user_name;
        Orders.mobile = this.user.phone;
        Orders.county = this.user.country;
        Orders.email = this.user.email;
        Orders.post_code = this.user.post_code;
        Orders.province = this.user.province;
        Orders.city = this.user.city;
        Orders.area = this.user.area;
        Orders.address = this.user.address;
        Orders.comment = this.user.comment;
        Orders.orderGoods = this.orderGoods;


        this.$axios.post(url,{Orders: Orders}).then((res) => {
            console.log(res)
        }).catch((err) => {

        })
      },
      deleteGoods(val) {
        this.orderGoods.splice(val.$index,1);
        console.log(this.orderGoods)
      },
      attrChange(scope) {
        let row = scope.row;
        let url = '/v1/order/order-attr';
        let param = {};
        param.attr_one = row.attr_one;
        row.attr_list.forEach((item,index) => {
          param.attr_one[index].attr = item.attr;
        })
        param.p_r_id = row.p_r_id;
        this.$axios.post(url,param).then((res) => {
          if(res.data.code == 200){
            let data = res.data.data;
            scope.row.img = data.img;
            scope.row.sku_code = data.sku_code;
          }
        }).catch((err) => {

        })
      },
      initPage(id) {
        this.orderId = id;
        let url = '/v1/order/details?id=' + 80000000;
        let that = this;
        this.$axios.get(url).then((res) => {
          console.log(res.data.data)
          console.log(res.data.data.orderGoods)
          if(res.data.code == 200){
            let order = res.data.data.order;
            //订单信息
            that.order.create_time = order.create_time;
            that.order.total_price = order.total_price;
            that.order.payment_type = order.payment_type;
            that.order.status = order.status;
            //用户信息
            that.user.user_name = order.user_name;
            that.user.phone = order.phone;
            that.user.email = order.email;
            that.user.country = order.country;
            that.user.province = order.province;
            that.user.city = order.city;
            that.user.area = order.area;
            that.user.address = order.address;
            that.user.post_code = order.post_code;
            that.user.comment = order.comment;
            that.user.user_ip = order.user_ip;
            that.orderGoods = res.data.data.orderGoods;
          }
        }).catch((err) => {

        })
      }
    },
    created() {
      let orderId = this.$route.query;
      this.initPage(orderId.id);
    }
  }
</script>

<style scoped lang="stylus">
  .container
    position relative
    .order_wrap
      position absolute
      left 0
      right: 0
      top: 0
      bottom: 0
      display flex
      flex-direction column
      .order_content
        padding 30px 30px 0
        flex 1
        overflow auto
        h2
          font-size: 20px
          margin 10px 0
          span
            margin-right 15px
            &.totalPrice
              color red
        .groupBox
          margin 50px 0
          table
            border-collapse collapse
            th
              width: 100px
              padding 10px
              background: #ccc
            td
              width: 300px
              padding 3px 5px
            .attrBox
              margin: 5px 0
            .w150
              width: 150px
    .footerButton
      position relative
      width: 100%
      padding 13px 30px
      box-sizing border-box
      background-color: hsla(0,0%,100%,.9);
      z-index 2
      button
        float: right;
</style>



